<template>
  <div style="height: 100%; width: 100%">
    <l-map style="z-index: 1; height: 100%; width: 100%" 
      :zoom="zoom" 
      :center="center" 
      @click="addMarker"
    >
      <l-tile-layer :url="url"></l-tile-layer>
      <l-marker
        :lat-lng="marker"
      />
      <l-polygon
        :lat-lngs="polygon.latlngs"
        :color="polygon.color"
      />
    </l-map>
  </div>
</template>

<script>

export default {
  data () {
    return {
      url: 'http://{s}.tile.osm.org/{z}/{x}/{y}.png',
      zoom: 10,
      center: [47.25, -1.319482],
      marker: [47.25, -1.319482],
      polygon: {
        latlngs: [[47.2263299, -1.6222], [47.21024000000001, -1.6270065], [47.1969447, -1.6136169], [47.18527929999999, -1.6143036], [47.1794457, -1.6098404], [47.1775788, -1.5985107], [47.1676598, -1.5753365], [47.1593731, -1.5521622], [47.1593731, -1.5319061], [47.1722111, -1.5143967], [47.1960115, -1.4841843], [47.2095404, -1.4848709], [47.2291277, -1.4683914], [47.2533687, -1.5116501], [47.2577961, -1.5531921], [47.26828069, -1.5621185], [47.2657179, -1.589241], [47.2589612, -1.6204834], [47.237287, -1.6266632], [47.2263299, -1.6222]],
        color: 'green'
      }
    };
  },
  methods: {
    addMarker(event) {
      this.marker = [];
      this.marker = event.latlng;
      this.checkArea();
      if(this.checkArea() == true){
        alert( "inside ");
      }
    },
    checkArea(){      
      var x = this.marker.lng;
      var y = this.marker.lat;
    
      var inside = false;
      for (var i = 0, j = this.polygon.latlngs.length - 1; i < this.polygon.latlngs.length; j = i++) {
        var xi = this.polygon.latlngs[i][1];
        var yi = this.polygon.latlngs[i][0];
        var xj = this.polygon.latlngs[j][1];
        var yj = this.polygon.latlngs[j][0];
        
        var intersect = ((yi > y) != (yj > y)) && (x < (xj - xi) * (y - yi) / (yj - yi) + xi);
        // console.log("this.marker",this.marker);
        // console.log("intersect",intersect);
        if (intersect) inside = !inside;
      }
      // console.log("inside",inside);
      return inside;
    }
  }
}
</script>